<template>
  <v-app style="background-color: rgb(247, 247, 247);" id="demo">
    <Header></Header>
    <v-btn @click="handleDown">pdf</v-btn>
    <div>
      <div style="text-align:center;font-size:30px;margin-top:30px;font-family:'bgtitlefamily';"> {{ title }} </div>
      <div style="margin: 20px 200px;">
        <v-card>
          <div v-for="(item, index) in questions" :key="index" style="margin:10px 40px;">
            <div v-if="index == 0" style="height:20px;"></div>
            <!-- 题目标号 -->
            <div style="font-size:20px;position:relative;top:10px;">
              <span style="color:red;" v-if="item.necessary==true || item.necessary=='true'">* </span>
              {{ (page-1)*4+index + 1 + '. ' }} {{ item.title }}
            </div>

            <!-- 多选题、多选投票题、多选报名题 -->
            <div v-if="item.type == '多选题' || item.type == '多选投票题' || item.type == '多选报名题'">
              <v-container>
              <div v-for="(subItem, i) in item.choices"
                    :key="i">
                <v-checkbox v-model="item.Answer" :label="subItem" hide-details
                :value="subItem"
                >
                <template v-slot:label>
                  <div  style="width:30vw;">
                    {{subItem}}
                  </div>
                </template>
                </v-checkbox>
                <p v-if="item.ptype==0" style="float:right;margin-top:-2.7vh;margin-right:26.2vw;color:red;" >0票</p>
                <p v-else-if="item.ptype==1" style="float:right;margin-top:-5vh;margin-right:26.2vw;color:red;" >已报名0人(剩余0人)</p>
              </div>
            </v-container>
            </div>
            <!-- 单选题、单选投票题、单选报名题 -->
            <div v-if="item.type == '单选题'||item.type == '单选投票题'||item.type == '单选报名题'" >
              <v-container>
                <v-radio-group v-model="item.answer">
                  <div v-for="(subItem, i) in item.choices" :key="i">
                    <v-radio :value="subItem">
                      <template v-slot:label>
                        <div  style="width:30vw;">
                          {{ subItem }}
                        </div>
                      </template>
                    </v-radio>
                    <span v-if="item.ptype==0" style="float:right;margin-top:-3.2vh;margin-right:21.2vw;color:red;" >0票</span>
                    <span v-else-if="item.ptype==1" style="float:right;margin-top:-5vh;margin-right:21.2vw;color:red;" >已报名0人(剩余0人)</span>
                  </div>
                </v-radio-group>
              </v-container>
            </div>
            <!-- 填空题 -->
            <div v-if="item.type == '填空题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-text-field
                    v-model="item.answer"
                    label="请输入你的答案"
                    clearable
                ></v-text-field>
              </div>
            </div>
            <!-- 打分题 -->
            <div v-if="item.type == '打分题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-rating
                  v-model="item.answer" style="margin-left:2vw;"
                  :length="item.limit"
                  color="yellow lighten-1"
                  background-color="grey lighten-1"
                  mid
              ></v-rating>
              </div>
            </div>
            <!-- 文本题 -->
            <div v-if="item.type == '文本题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-textarea style="width:30vw;margin-left:0vw;margin-top:1vh;" dense
                    v-model="item.answer"
                    label="请输入你的答案"
                    clearable
                ></v-textarea>
              </div>
            </div>
            <!-- 下拉框 -->
            <div v-if="item.type == '下拉框'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-select
                    v-model="item.answer"
                    :items="item.choices"
                    item-text="option"
                    item-value="abbr"
                    label="请选择你的选择"
                    persistent-hint
                    return-object
                    single-line
                ></v-select>
              </div>
            </div>
            <div v-if="item.type == '定位题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-text-field
                    v-model="item.answer"
                    label="" solo
                    clearable
                ></v-text-field>
              </div>
            </div>
            <!-- 时间选择器 -->
            <div v-if="item.type == '时间选择器'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
              <v-menu
                  v-model="item.menu"
                  :close-on-content-click="false"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      append-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      outlined
                      dense v-model="item.date"
                      background-color="white"
                      style="width: 12vw; margin-left: 0"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                v-model="item.date" @input="item.menu = false"></v-date-picker>
                </v-menu>
                <v-menu
                  ref="menu"
                  v-model="item.menu1"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="time"
                  offset-y
                  max-width="290px"
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="item.time"
                      append-icon="mdi-clock-time-four-outline"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      outlined
                      background-color="white"
                      dense
                      style="width: 12vw;float:right;margin-top:-9vh;"
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-model="item.time"
                    full-width
                    @click:minute="item.menu1 = false"
                  ></v-time-picker>
                </v-menu>
              </div>
            </div>
          </div>
          <!-- 翻页 -->
          <div style="text-align:center;">
            <v-btn @click="back"
                  style="background-color:skyblue;width:100px;margin:30px 2vw 2vh 2vw;color:white;">返&nbsp;&nbsp;回</v-btn>
          </div>
        </v-card>
      </div>
    </div>
  </v-app>
</template>

<script>
import "@/assets/font/font.css";
import htmlToPdf from '@/plugins/htmlToPdf';
import Header from '@/components/Header';
export default {
  name: 'Preview',
  components: {
      Header,
  },
  data() {
      return {
          id: '',
          title: '这是一个调查',
          page:1,
          id:-1,
          return: '',
          menu:'',
          totalPage:1,
          questions:[],
          question: [
            {
              type: '单选题',
              title: '单选题',
              choice: [ 'A', 'B', 'C', 'D' ],
              limit: '',
              necessary: true,
            }
          ],
      }
  },
  mounted(){
    this.return = this.$route.query.return

    this.questions=JSON.parse(this.$route.query.question)
    this.title=this.$route.query.title
    this.id=this.$route.query.id
    this.question=[]
    for(var i=0;i<this.questions.length;i++) this.questions[i].Answer=[]
    if(this.questions.length%4==0) this.totalPage=this.questions.length/4
    else if(this.questions.length%4!=0) this.totalPage=parseInt(this.questions.length/4)+1
    for(var i=0;i<4&&i<this.questions.length;i++)  this.question.push(this.questions[i])
    // console.log(this.$route.query.title,JSON.parse(this.$route.query.question));
    if(this.$route.query.download == 1) {
      // this.handleDown();
      setTimeout(() => {
        this.$router.replace('/manage');
        this.handleDown();
      }, 1);

    }
  },
  methods: {
    back(){
      if(this.return == 'manage') {
        this.$router.replace('/manage');
      } else {
        this.$router.replace({path:this.$route.query.url,query:{type:0,title:this.title,question:JSON.stringify(this.questions)}})
      }
    },
    lastPage(){
        if(this.page==1) {
            this.$message.warning('这已经是第一页了')
        }
        else{
            this.question=[]
            this.page--
            for(var i=(this.page-1)*4;i<this.page*4&&i<this.questions.length;i++){
                this.question.push(this.questions[i])
            }
        }
    },
    nextPage(){
      if(this.page==this.totalPage) {
          this.$message.warning('这已经是最后一页了')
      }
      else{
          this.question=[]
          this.page++
          for(var i=(this.page-1)*4;i<this.page*4&&i<this.questions.length;i++){
              this.question.push(this.questions[i])
          }
      }
    },
    handleDown(){
      htmlToPdf.downloadPDF( document.querySelector('#demo'),'我的PDF');
    },
  }
}
</script>